import Vue from 'vue'
import Router from 'vue-router'
import NavBanner from '@/components/NavBanner'
import AboutMe from '@/components/AboutMe'
import ContactMe from '@/components/ContactMe'
import WorkDetail from '@/components/WorkDetail'
import login from '@/components/login'
import store from '../vuex/store.js'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },{
      path: '/home',
      name: 'NavBanner',
      component: NavBanner,
      beforeEnter: (to, from, next) => {
        if(store.state.username||store.state.userpass){
            next()
        }else{
            next({
              path:'/',
              replace:true
            })
        }
      }
    },{
      path: '/about',
      name: 'AboutMe',
      component: AboutMe,
      beforeEnter: (to, from, next) => {
        if(store.state.username||store.state.userpass){
          next()
        }else{
          next({
            path:'/',
            replace:true
          })
        }
      }
    },{
      path: '/contact',
      name: 'ContactMe',
      component: ContactMe,
      beforeEnter: (to, from, next) => {
        if(store.state.username||store.state.userpass){
          next()
        }else{
          next({
            path:'/',
            replace:true
          })
        }
      }
    },{
      path: '/work',
      name: 'WorkDetail',
      component: WorkDetail,
      beforeEnter: (to, from, next) => {
        if(store.state.username||store.state.userpass){
          next()
        }else{
          next({
            path:'/',
            replace:true
          })
        }
      }
    }
  ]
})



